<template><section><h1>switch 开关组件</h1>
<blockquote>
<p>基础用法</p>
</blockquote>
<demo-block>
                  <div class="source" slot="source"><template>
    <switch-component :xchange="switchChange" :init-data="switchInitData" :checked-type="checkedType">
    </switch-component> {{checkedType}}
</template>

</div>
                  <h3>通过 <code>input 中 type=&quot;button&quot;</code> 和 css3 过渡的方式实现</h3>

                  <div class="highlight" slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">switch-component</span> 
        <span class="hljs-attr">:xchange</span>=<span class="hljs-string">"switchChange"</span> 
        <span class="hljs-attr">:init-data</span>=<span class="hljs-string">"switchInitData"</span> 
        <span class="hljs-attr">:checked-type</span>=<span class="hljs-string">"checkedType"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">switch-component</span>&gt;</span> {{checkedType}}
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { SwitchComponent } <span class="hljs-keyword">from</span> <span class="hljs-string">'vo-ui'</span>
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: {
        SwitchComponent
    },
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkedType</span>: <span class="hljs-literal">true</span>,
        };
    },
    <span class="hljs-attr">methods</span>: {
        <span class="hljs-comment">// switch 开关事件</span>
        switchChange(e) {
            <span class="hljs-keyword">this</span>.checkedType = !e
        },
        <span class="hljs-comment">// 初始化给的 开关数据</span>
        switchInitData(e) {
            <span class="hljs-keyword">this</span>.checkedType = e
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<blockquote>
<h2>开关配置</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>:xchange</td>
<td style="text-align:left">switch 开关事件</td>
</tr>
<tr>
<td>:init-data</td>
<td style="text-align:left">回显，初始化给的 开关数据，显示了当前状态</td>
</tr>
<tr>
<td>:checked-type</td>
<td style="text-align:left">开关 是否 打开的状态，需要需要传进去</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import { SwitchComponent } from 'vo-ui'
export default {
    components: {
        SwitchComponent
    },
    data() {
        return {
            checkedType: true,
        };
    },
    methods: {
        // switch 开关事件
        switchChange(e) {
            this.checkedType = !e
        },
        // 初始化给的 开关数据
        switchInitData(e) {
            this.checkedType = e
        }
    }
};
</script>


